.details-container {
    margin:auto;
    display:grid;
    grid-template-columns: repeat(1, 1fr);  /*1열*/
    max-width : 100%;
    padding: 2.5rem 0;
    gap:2.5rem;
}
.img-container {
    grid-row-start: 2;
}
.img-container:hover .img-style {
    transform:scale(1.05);
}
.img-wrapper {
    height:24rem;
    overflow: hidden;
    border-radius: 12px;
    position:relative;
}
.img-style {
    width:100%;
    height:100%;
    object-fit: cover;
    display:block;
    transition :transform 0.5s;
}
.content-container {
    display:flex;
    flex-direction:column;
    gap:12px;
    
}
.text-publisher {
    color:darkgreen;
    font-weight:600;
}
.text-title {
    color:black;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

}
.favorites-btn {
    display:inline-block;
    background: black;
    color:white;
    padding: 0.75rem 2rem;
    border-radius: 0.5rem;
    letter-spacing: 1.5px;
    font-size: large;
}
.recipe-title {
    color:black;
    font-weight: 600;
}
.recipe-content {
    display:flex;
    flex-direction: column;
    gap:8px;
}

@media (min-width:1024px) {
    .details-container {
        grid-template-columns: repeat(2, 1fr); /*2열로 반응형*/
    }
    .img-container {
        grid-row-start:auto;
    }
}